body{
  background: url('./assets/classroom.jpg');
  background-repeat: repeat-y;
  background-size: 100%;
}
.App{
  background-color: rgb(27, 61, 61);
  opacity: 0.95;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}
.rules{
  color: #fff;
  font-size: 12px;
  text-align: center;
  padding: 10px;
  height: 8px;
}
#to-do-form input{
  background-color: rgb(27,112,137);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color: #fff;
}
#to-do-form input::placeholder{
  color: rgba(255, 255, 255, 0.5)
}
#to-do-form input:focus{
  outline: none;
}
#to-do-form button{
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border: 0;
  background-color: rgb(202,156,24);
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
}